.totop {
  width: 72px;
  height: 72px;
  right: 24px;
  bottom: 60px;
  border-radius: 48px;
  box-shadow: 0px 2px 16px 1px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
}

.totop:hover {
  background: #FEDB9A;
}

.footer {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  z-index: 800;
}

.footer-link {
  text-align: center;
  color: #181818 !important;
}

.footer-link:hover {
  color: #FEDB9A !important;
}
